                <div class="about-me-header">
                    <h1 class="about-me-title fade-in-ams">about me</h1>
                    <div class="about-section-line"></div>
                </div>

                <div class="about-me-flex-container">

                    <div class="about-me-container">
                        <div class="about-me-info">
                            <div class="about-me-info-paragraph">
                                <p class="about-paragraph fade-in-ams">Hi, my name is Kipkorir Amos, a Fullstack developer. I enjoy building things for the web.</p>
                                <p class="about-paragraph fade-in-ams"> I create websites and applications  that engage users.I love creating beautiful things that live on the web. I seek to create light, minimalistic <br>
                            animated websites and mobile features that solve user problems through effective solutions.</p>
                                <p class="fade-in-ams">Some of the technologies I have been using :</p>
                                
                                <ul class="tech-ul fade-in-ams">
                                    <li>React.js</li>
                                    <li>Vue.js</li>
                                    <li>TypeScript</li>
                                    <li>REST APIs</li>
                                    <li>Git</li>
                                    <li>Javascript</li>
                                    <li>Flask</li>
                                    <li>Python</li>
                                </ul>

         
                                
                                
                         
